<template>
	<view>
		<view class="topBackGround">
			<view class="nullBG">
				&nbsp;
			</view>
		</view>
		<view class="topCard">
						<uni-grid :column="3" :highlight="true" :showBorder="false" :square="false" @change="topCardClick">
				<uni-grid-item v-for="(item, index) in topList" :index="index" :key="index">
					<view class="topCardItem">
						<view class="topCardItemBorder" :style="{'border-right':index<2?'#8799a3 solid 1rpx':'0rpx'}">
							<text class="count">{{item.count}}</text>
							<text class="tips">{{item.tips}}</text>
						</view>
					</view>
				</uni-grid-item>
			</uni-grid>
		</view>
		<view class="searchBox">
			<uni-search-bar v-model="searchValue" bgColor="#ffffff" :focus="false" @confirm="searchClick">
			</uni-search-bar>
		</view>
		<view v-for="(item,index) in formList" :key="item.contractNo" class="formCard">
			<uni-card style="box-shadow: 5rpx 5rpx 15rpx #00ffff" title="物流单"
				:subTitle="item.contractNo" :extra="item.factoryName" @click="onClick">
				<view class="cardContent">
					<uni-row style="width: 100%;">
						<uni-col :span="8">
							出库车牌号 :
						</uni-col>
						<uni-col :span="16">
							{{item.carNo}}
						</uni-col>
					</uni-row>
					<uni-row style="width: 100%;">
						<uni-col :span="8">
							出库货运量 :
						</uni-col>
						<uni-col :span="16">
							{{item.goodsCount}}
						</uni-col>
					</uni-row>
					<uni-row style="width: 100%;">
						<uni-col :span="8">
							运费 :
						</uni-col>
						<uni-col :span="16">
							{{item.freight}}
						</uni-col>
					</uni-row>
				</view>
				<view class="auditStatusGroup">
					<view v-for="(statusItem,statusIndex) in directorList" :key="statusItem.directorId"
						class="auditStatusItem">
						<view class="card-actions-item-text">{{statusItem.directorName}}</view>
						<view v-show="statusIndex==0">
							<uni-icons :style="'margin-left:15rpx'"
								:type="item.firstState === 0 ? '' : item.firstState===1?'checkmarkempty':'closeempty'"
								size="24" color="#0081ff"></uni-icons>
						</view>
						<view v-show="statusIndex==1">
							<uni-icons :style="'margin-left:15rpx'"
								:type="item.secondState === 0 ? '' : item.secondState===1?'checkmarkempty':'closeempty'"
								size="24" color="#0081ff"></uni-icons>
						</view>
						<view v-show="statusIndex==2">
							<uni-icons :style="'margin-left:15rpx'"
								:type="item.thridState === 0 ? '' : item.thridState===1?'checkmarkempty':'closeempty'"
								size="24" color="#0081ff"></uni-icons>
						</view>
					</view>
				</view>
				<view slot="actions" class="actionGroup">
					<view class="actionGroupItem" @click="actionsClick('pass')">
						<button class="buttonGroup" type="primary">通过</button>
					</view>
					<view class="actionGroupItem" @click="actionsClick('refuse')">
						<button class="buttonGroup" type="warn">拒绝</button>
					</view>
					<view class="actionGroupItem" @click="actionsClick('more')">
						<button class="buttonGroup">详情</button>
					</view>
				</view>
			</uni-card>
			<uni-card v-show="item.type==2" style="box-shadow: 5rpx 5rpx 15rpx #00ffff" title="海运物流单"
				:subTitle="item.contractNo" :extra="item.CarrierCompanyName" @click="onClick">
				<view class="cardContent">
					<uni-row style="width: 100%;">
						<uni-col :span="8">
							集装箱号 :
						</uni-col>
						<uni-col :span="16">
							{{item.containerNo}}
						</uni-col>
					</uni-row>
					<uni-row style="width: 100%;">
						<uni-col :span="8">
							铅封号 :
						</uni-col>
						<uni-col :span="16">
							{{item.sealNo}}
						</uni-col>
					</uni-row>
					<uni-row style="width: 100%;">
						<uni-col :span="8">
							费用 :
						</uni-col>
						<uni-col :span="16">
							{{item.expenses}}
						</uni-col>
					</uni-row>
				</view>
				<view class="auditStatusGroup">
					<view v-for="(statusItem,statusIndex) in directorList" :key="statusItem.directorId"
						class="auditStatusItem">
						<view class="card-actions-item-text">{{statusItem.directorName}}</view>
						<view v-show="statusIndex==0">
							<uni-icons :style="'margin-left:15rpx'"
								:type="item.firstState === 0 ? '' : item.firstState===1?'checkmarkempty':'closeempty'"
								size="24" color="#0081ff"></uni-icons>
						</view>
						<view v-show="statusIndex==1">
							<uni-icons :style="'margin-left:15rpx'"
								:type="item.secondState === 0 ? '' : item.secondState===1?'checkmarkempty':'closeempty'"
								size="24" color="#0081ff"></uni-icons>
						</view>
						<view v-show="statusIndex==2">
							<uni-icons :style="'margin-left:15rpx'"
								:type="item.thridState === 0 ? '' : item.thridState===1?'checkmarkempty':'closeempty'"
								size="24" color="#0081ff"></uni-icons>
						</view>
					</view>
				</view>
				<view slot="actions" class="actionGroup">
					<view class="actionGroupItem" @click="actionsClick('pass')">
						<button class="buttonGroup" type="primary">通过</button>
					</view>
					<view class="actionGroupItem" @click="actionsClick('refuse')">
						<button class="buttonGroup" type="warn">拒绝</button>
					</view>
					<view class="actionGroupItem" @click="actionsClick('more')">
						<button class="buttonGroup">详情</button>
					</view>
				</view>
			</uni-card>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				directorList: [{
						directorId: "00001",
						directorName: "黄佳森"
					},
					{
						directorId: "00002",
						directorName: "谢晓东"
					},
					{
						directorId: "00003",
						directorName: "林国丰"
					}
				],
				topList: [{
						count: 7,
						tips: "待审批"
					},
					{
						count: 2,
						tips: "已审批"
					},
					{
						count: 10,
						tips: "已完成"
					}
				],
				formList: [
					{
						contractNo: "TR00172839456",
						factoryName: "1号仓库",
						carNo: "桂C1431",
						goodsCount: 200,
						freight: 4000,
						firstState: 1,
						secondState: 0,
						thridState: 2
					},
					{
						contractNo: "TR00172839456",
						factoryName: "1号仓库",
						carNo: "桂C1431",
						goodsCount: 200,
						freight: 4000,
						firstState: 1,
						secondState: 1,
						thridState: 2
					},
					{
						contractNo: "TR00172839456",
						factoryName: "1号仓库",
						carNo: "桂C1431",
						goodsCount: 200,
						freight: 4000,
						firstState: 0,
						secondState: 1,
						thridState: 0
					}
				],
			}
		},
		onReachBottom() {
			this.formList = [...this.formList, ...this.formList]
		},
		methods: {
			topCardClick() {
				console.log("点击");
			},
			searchClick(res) {
				uni.showToast({
					title: '搜索：' + res.value,
					icon: 'none'
				})
			},
			onClick(e) {
				console.log(e)
			},
		}
	}
</script>

<style>
	.topBackGround {
		z-index: 100;
		background-repeat: no-repeat;
		background-size: 100%;
		background-position-y: 0px;
		background-image: url(/static/UIImages/OA.png);
		margin-bottom: 90rpx;
	}
	
	.nullBG {
		height: 330rpx;
	}
	
	.topCard {
		display: flex;
		align-items: center;
		flex-direction: row;
		justify-content: center;
		top: 210rpx;
		height: 165rpx;
		width: 92%;
		margin-left: 3.5%;
		border-radius: 30rpx;
		border: #00ffff solid 5rpx;
		background-color: #ffffff;
		box-shadow: -5rpx 5rpx 15rpx #00ffff;
		position: absolute;
	}
	
	.topCardItem {
		height: 150rpx;
		border-radius: 50rpx;
		flex: 1;
	}
	
	.topCardItemBorder {
		position: relative;
		/* #ifndef APP-NVUE */
		display: flex;
		/* #endif */
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	
	.count {
		color: #0081ff;
		font-size: 50rpx;
		margin-bottom: 3%;
	}
	
	.tips {
		color: #8799a3;
	}
	
	.formCard {
		/* margin-top: 90rpx; */
	}

	.cardContent {
		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: center;
	}

	.auditStatusGroup {
		width: 100%;
		display: flex;
		align-items: center;
		flex-direction: row;
		justify-content: center;
		border-top: #DCDCDC solid 1rpx;
		margin-top: 20rpx;
	}

	.auditStatusItem {
		width: 100%;
		display: flex;
		align-items: center;
		flex-direction: row;
		justify-content: center;
		margin-top: 20rpx;
	}

	.actionGroup {
		display: flex;
		align-items: center;
		flex-direction: row;
		justify-content: center;
	}

	.actionGroupItem {
		width: 100%;
		display: flex;
		align-items: center;
		flex-direction: row;
		justify-content: center;
		border-top: #DCDCDC solid 1rpx;
	}

	.buttonGroup {
		width: 100%;
		height: 80rpx;
		font-size: 30rpx;
		margin: 20rpx;
		display: flex;
		align-items: center;
		flex-direction: row;
		justify-content: center;
	}
</style>
